body {
    color: rgb(110, 105, 105);
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .hidden {
    display: none;
  }
  .container {
    padding: 30px;
  }
  h3 {
    text-transform: uppercase;
    font-size: 20px;
    text-align: center;
  }
  .cardList {
    list-style-type: none;
    display: flex;
    justify-content: center;
  }
  .card-item {
    position: relative;
    margin-right: 20px;
    width: 70px;
    height: 40px;
    border-radius: 5px;
    background-color: white;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    filter: grayscale(100%);
  }
  .active {
    filter: grayscale(0%);
  }
  .cardVisa {
    background-image: url(../img/visa_electron.png);
    
  }
  .cardMastercard {
    background-image: url(../img/mastercard.png);
  }
  .cardMir {
    background-image: url(../img/mir.png);
  }
  .cardAmericanExpress {
    background-image: url(../img/american_express.png);
  }
  .cardUnionPay {
    background-image: url(../img/unionpay.png);
  }
  .cardMaestro {
    background-image: url(../img/maestro.png);
  }
  .card-form-widget {
    display: flex;
    justify-content: center;
  }
  #numberCard-input {
    height: 25px;
    display: inline-block;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    color: #495057;
    border: 1px solid #ccc;
    border-radius: 0.5rem;
  }
  
  .submit {
    cursor: pointer;
    color: white;
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    user-select: none;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 5px;
    border: 1px solid #4cae4c;
    background-color: #5cb85c;
    margin-left: 10px;
  }
  .btn:hover {
    background-color: #235723;
  }